<template>
  <div class="page-home page">

    <div class="header">
      <div class="wrapper">
        <div class="index" ref="index"></div>
      </div>
    </div>
    <div class="footer">
      <div class="wrapper">
       <p>
        2017 185
         <br>
         <br>
         <small class="note">20171010XX</small>
      </p>
      </div>
    </div>

  </div>
</template>
<script>
import animateObj from './page-title.js'
export default {
  name: 'pageHome',
  data () {
    return {
      svg: null,
      animateObj: animateObj
    }
  },
  mounted () {
    this.$nextTick(() => {
      this.animateObj.init(this.$refs.index)
      this.animateObj.start()
    })
  }
}
</script>
<style scoped lang="scss">

.header{
  height: 370px;
  background: #5b1112;
}
.footer p {
  max-width: 560px;
  margin: 0 auto;
  font-size: 16px;
  line-height: 1.6;
  margin-bottom: 30px;
    &:first-child{
      margin-top: 60px;
    }
}
.header .wrapper,
.header .index{
  height: 100%;
}
</style>



